// WordPress dependencies
import { PanelBody, PanelRow, RangeControl, SelectControl, ToggleControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import ImagePicker from '../elements/image-picker';
import ProFeatureControl from '../elements/pro-feature-control';

const coreAssets = window.COCA_BAIS_ASSETS;

export default ( props ) => {
	const isTripleEnabled = !! props.proSettings.is_triple_active;
	const sliderType = props.attributes.slider_type;
	const isTriple = isTripleEnabled && sliderType === 'triple';

	const OffsetField = (
		<div className={ 'field-control field-inline' }>
			<span className="field-control__label">
				{ __( 'DIVIDER DEFAULT OFFSET', 'wp-before-after-image-slider' ) }
			</span>
			<div className={ 'fields-wrapper inline' }>
				<RangeControl
					initialPosition={ 50 }
					max={ 100 }
					min={ 0 }
					value={ parseInt( props.attributes.default_offset ) }
					onChange={ ( value ) => props.updateAttributes( { default_offset: value } ) }
				/>
			</div>
		</div>
	);

	return (
		<PanelBody
			title={ __( 'Settings', 'wp-before-after-image-slider' ) }
			initialOpen={ false }
			opened={ props.attributes.settings_panel_open }
			onToggle={ () => props.updateAttributes( {
				settings_panel_open: ! props.attributes.settings_panel_open
			} ) }
		>
			<PanelRow>
				{ ! isTriple && (
					<>
						<ProFeatureControl { ...props } value={ 'default_offset' }>
							{ OffsetField }
						</ProFeatureControl>
						<div className={ 'field-control field-inline' }>
							<span className="field-control__label">
								{ __( 'TRIGGER TYPE', 'wp-before-after-image-slider' ) }
							</span>
							<div className={ 'fields-wrapper inline' }>
								<SelectControl
									value={ props.attributes.trigger_type || 'drag' }
									onChange={ ( value ) => props.updateAttributes( {
										trigger_type: value,
									} ) }
									options={ [
										{ label: __( 'Drag', 'wp-before-after-image-slider' ), value: 'drag' },
										{ label: __( 'Hover', 'wp-before-after-image-slider' ), value: 'hover' },
									] }
								/>
							</div>
						</div>
						<div className={ 'field-control field-inline' }>
							<span className="field-control__label">
								{ __( 'SLIDER ORIENTATION', 'wp-before-after-image-slider' ) }
							</span>
							<div className={ 'fields-wrapper inline' }>
								<ImagePicker
									options={ [
										{
											value: 'horizontal',
											label: __( 'Horizontal', 'wp-before-after-image-slider' ),
											imageUrl: coreAssets.orientation.hr,
										},
										{
											value: 'vertical',
											label: __( 'Vertical', 'wp-before-after-image-slider' ),
											imageUrl: coreAssets.orientation.vr,
										},
									] }
									value={ props.attributes.orientation || 'horizontal' }
									onChange={ ( value ) => props.updateAttributes( {
										orientation: value,
									} ) }
								/>
							</div>
						</div>
					</>
				) }
				<div className={ 'field-control field-inline' }>
					<span className="field-control__label">
						{ __( 'SHOW OVERLAY', 'wp-before-after-image-slider' ) }
					</span>
					<div className={ 'fields-wrapper inline' }>
						<ToggleControl
							label={ '' }
							checked={ props.attributes.show_overlay || false }
							onChange={ ( value ) => props.updateAttributes( {
								show_overlay: value,
							} ) }
						/>
					</div>
				</div>
				<div className={ 'field-control field-inline' }>
					<span className="field-control__label">
						{ __( 'SHOW Labels', 'wp-before-after-image-slider' ) }
					</span>
					<div className={ 'fields-wrapper inline' }>
						<ToggleControl
							label={ '' }
							checked={ props.attributes.show_labels || false }
							onChange={ ( value ) => props.updateAttributes( { show_labels: value } ) }
						/>
					</div>
				</div>

				<ProFeatureControl { ...props } value={ 'slider_template' }>
					<div className={ 'field-control field-inline' }>
						<span className="field-control__label">
							{ __( 'SLIDER TEMPLATE', 'wp-before-after-image-slider' ) }
						</span>
						<div className={ 'fields-wrapper inline' }>
							<ImagePicker
								options={ [
									{
										value: 'default',
										label: 'default',
										imageUrl: coreAssets.templates.st01,
									},
									{
										value: 'style01',
										label: 'Item 1',
										imageUrl: coreAssets.templates.st02,
									},
									{
										value: 'style02',
										label: 'Item 2',
										imageUrl: coreAssets.templates.st03,
									},
									{
										value: 'style03',
										label: 'Item 3',
										imageUrl: coreAssets.templates.st04,
									},
									{
										value: 'style04',
										label: 'Item 4',
										imageUrl: coreAssets.templates.st05,
									},
									{
										value: 'style05',
										label: 'Item 5',
										imageUrl: coreAssets.templates.st06,
									},
									{
										value: 'style06',
										label: 'Item 6',
										imageUrl: coreAssets.templates.st07,
									},
									{
										value: 'style07',
										label: 'Item 7',
										imageUrl: coreAssets.templates.st08,
									},
								] }
								value={ props.attributes.slider_template || 'default' }
								onChange={ ( value ) => props.updateAttributes( {
									slider_template: value,
								} ) }
							/>
						</div>
					</div>
				</ProFeatureControl>

				{ props.attributes.slider_type === 'default' && (
					<ProFeatureControl { ...props } value={ 'slider_template' }>
						<div className={ 'field-control field-inline' }>
							<span className="field-control__label">
								{ __( 'SHOW POPUP', 'wp-before-after-image-slider' ) }
							</span>
							<div className={ 'fields-wrapper inline' }>
								<ToggleControl
									label={ '' }
									checked={ props.attributes.show_popup || false }
									onChange={ ( value ) => props.updateAttributes( {
										show_popup: value,
									} ) }
								/>
							</div>
						</div>
						{ props.attributes.show_popup && (
							<div className={ 'field-control field-inline' }>
								<span className="field-control__label">
									{ __( 'POPUP POSITION', 'wp-before-after-image-slider' ) }
								</span>
								<div className={ 'fields-wrapper inline' }>
									<SelectControl
										value={ props.attributes.popup_position }
										onChange={ ( value ) => props.updateAttributes( {
											popup_position: value,
										} ) }
										options={ [
											{ label: __( 'Top Left', 'wp-before-after-image-slider' ), value: 'top-left', },
											{ label: __( 'Top Right', 'wp-before-after-image-slider' ), value: 'top-right', },
											{ label: __( 'Bottom Left', 'wp-before-after-image-slider' ), value: 'bottom-left', },
											{ label: __( 'Bottom Right', 'wp-before-after-image-slider' ), value: 'bottom-right', },
										] }
									/>
								</div>
							</div>
						) }
					</ProFeatureControl>
				) }
			</PanelRow>
		</PanelBody>
	);
};
