// WordPress dependencies
// eslint-disable-next-line @wordpress/no-unsafe-wp-apis
import { __experimentalUnitControl as UnitControl, PanelBody, PanelRow, RangeControl, } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

// Internal dependencies
import ProFeatureControl from '../elements/pro-feature-control';
import { getNumberFromString } from '../../utils';
import ColorControl from '../control/color-control';

export default ( props ) => {
	const getAttr = ( attribute ) => {
		const store = props.attributes;
		return !! store[ attribute ] ? store[ attribute ] : '';
	};

	const BorderColorField = ColorControl( {
		props,
		attribute: 'divider_color_normal',
		labelText: __( 'Default', 'wp-before-after-image-slider' ),
		resetText: __( 'Clear', 'wp-before-after-image-slider' ),
	} );

	return (
		<PanelBody
			title={ __( 'Divider', 'wp-before-after-image-slider' ) }
			initialOpen={ false }
			opened={ props.attributes.divider_panel_open }
			onToggle={ () => props.updateAttributes( { divider_panel_open: ! props.attributes.divider_panel_open, } ) }
		>
			<PanelRow>
				<ProFeatureControl { ...props } value={ 'divider_color_normal' }>
					<div className={ 'field-control field-inline' }>
						<span className="field-control__label">
							{ __( 'COLOR:', 'wp-before-after-image-slider' ) }
						</span>
						<div className={ 'fields-wrapper inline' }>
							<div className={ 'field' }>{ BorderColorField }</div>
						</div>
					</div>
					<div className={ 'field-control field-inline' }>
						<span className="field-control__label">
							{ __( 'WIDTH:', 'wp-before-after-image-slider' ) }
						</span>
						<div className={ 'fields-wrapper inline' }>
							<div
								className={ 'group-field' }
								style={ { width: '100%', display: 'flex', gap: '10px', marginRight: '12px', } }>
								<UnitControl
									isPressEnterToChange
									onChange={ ( value ) => props.updateAttributes( { divider_width: value, } ) }
									value={ getAttr( 'divider_width' ) }
								/>
								<RangeControl
									onChange={ ( value ) => props.updateAttributes( { divider_width: value, } ) }
									max={ 100 }
									min={ 0 }
									step={ 1 }
									allowReset={ true }
									withInputField={ false }
									value={ getNumberFromString( getAttr( 'divider_width' ) ) }
								/>
							</div>
						</div>
					</div>
				</ProFeatureControl>
			</PanelRow>
		</PanelBody>
	);
};
