// WordPress dependencies
import { PanelBody, PanelRow } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

// Internal dependencies
import MediaUploader from '../elements/media-uploader';
import ProFeatureControl from '../elements/pro-feature-control';

export default ( props ) => (
	<PanelBody
		title={ __( 'Indicator Image', 'wp-before-after-image-slider' ) }
		initialOpen={ false }
		opened={ props.attributes.indicator_panel_open }
		onToggle={ () => props.updateAttributes( { indicator_panel_open: ! props.attributes.indicator_panel_open, } ) }
	>
		<PanelRow>
			<ProFeatureControl { ...props } value={ 'indicator_image' }>
				<div className={ 'field-control field-inline' }>
					<span className="field-control__label">
						{ __( 'IMAGE', 'wp-before-after-image-slider' ) }
					</span>
					<div className={ 'fields-wrapper inline' }>
						<MediaUploader
							value={ props.attributes.indicator_image }
							onChange={ ( attachment ) => props.updateAttributes( { indicator_image: attachment, } ) }
						/>
					</div>
				</div>
			</ProFeatureControl>
		</PanelRow>
	</PanelBody>
);
