// WordPress dependencies
import { ExternalLink, Disabled } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

export default function( props ) {
	if ( ! props.proFeatureEnabled && props.proFeatures && props.proFeatures.includes( props.value ) ) {
		return (
			<div className={ 'coca-disable-feature' } style={ props.style }>
				<Disabled>{ props.children }</Disabled>
				<div className={ 'buy-pro-overlay' }>
					<ExternalLink href={ props.buyPro } className="components-button is-primary">
						{ __( 'BUY PRO', 'wp-before-after-image-slider' ) }
					</ExternalLink>
				</div>
			</div>
		);
	}

	return props.children;
}
