// WordPress dependencies
// eslint-disable-next-line @wordpress/no-unsafe-wp-apis
import { __experimentalBoxControl as BoxControl, __experimentalUnitControl as UnitControl, Button, ColorIndicator, GradientPicker, PanelBody, PanelRow, RangeControl, SelectControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';

// Internal dependencies
import ProFeatureControl from '../elements/pro-feature-control';
import React from 'react';
import { getNumberFromString, ucFirst } from '../../utils';
import ColorControl from '../control/color-control';

export default ( props ) => {
	const [ toggleOptions, updateToggleOptions ] = useState( {} );
	const isTripleEnabled = !! props.proSettings.is_triple_active;
	const sliderType = props.attributes.slider_type;
	const isTriple = isTripleEnabled && sliderType === 'triple';

	const getAttr = ( attribute ) => {
		const store = props.attributes;
		return !! store[ attribute ] ? store[ attribute ] : '';
	};

	const verticalAlignments = [
		{ label: __( 'Left', 'wp-before-after-image-slider' ), value: 'left' },
		{ label: __( 'Center', 'wp-before-after-image-slider' ), value: 'middle' },
		{ label: __( 'Right', 'wp-before-after-image-slider' ), value: 'right' },
	];
	const horizontalAlignments = [
		{ label: __( 'Top', 'wp-before-after-image-slider' ), value: 'top' },
		{ label: __( 'Center', 'wp-before-after-image-slider' ), value: 'middle' },
		{ label: __( 'Bottom', 'wp-before-after-image-slider' ), value: 'bottom' },
	];

	const LabelAlignmentControls = () => (
		<div className={ 'field' }>
			{ props.attributes.orientation === 'horizontal' && (
				<SelectControl
					onChange={ ( value ) => props.updateAttributes( { label_horizontal_alignment: value, } ) }
					value={ getAttr( 'label_horizontal_alignment' ) }
					options={ horizontalAlignments }
				/>
			) }
			{ props.attributes.orientation === 'vertical' && (
				<SelectControl
					onChange={ ( value ) => props.updateAttributes( { label_vertical_alignment: value, } ) }
					value={ getAttr( 'label_vertical_alignment' ) }
					options={ verticalAlignments }
				/>
			) }
		</div>
	);

	const isDefaultState = ! isTriple && props.attributes.slider_template === 'default';
	const LabelAlign = isDefaultState && (
		<div className={ 'field-control field-inline' }>
			<span className="field-control__label">
				{ __( 'ALIGN:', 'wp-before-after-image-slider' ) }
			</span>
			<div className={ 'fields-wrapper inline' }>
				<div className={ 'group-field' } style={ { width: '100%', display: 'flex', gap: '10px', marginRight: '12px', } }>
					<RangeControl
						onChange={ ( value ) => props.updateAttributes( { label_alignment: value, } ) }
						max={ 100 }
						min={ 0 }
						step={ 1 }
						allowReset={ true }
						withInputField={ false }
						value={ props.attributes.handle_border_radius }
					/>
				</div>
			</div>
		</div>
	);

	const LabelTextColorsField = (
		<>
			<div className={ 'field' }>
				{ ColorControl( {
					props,
					attribute: 'label_text_color_before',
					labelText: __( 'Before', 'wp-before-after-image-slider' ),
					resetText: __( 'Clear', 'wp-before-after-image-slider' ),
				} ) }
			</div>
			<div className={ 'field' }>
				{ isTriple &&
					ColorControl( {
						props,
						attribute: 'label_text_color_middle',
						labelText: __( 'Middle', 'wp-before-after-image-slider' ),
						resetText: __( 'Clear', 'wp-before-after-image-slider' ),
					} ) }
			</div>
			<div className={ 'field' }>
				{ ColorControl( {
					props,
					attribute: 'label_text_color_after',
					labelText: __( 'After', 'wp-before-after-image-slider' ),
					resetText: __( 'Clear', 'wp-before-after-image-slider' ),
				} ) }
			</div>
		</>
	);
	const LabelBackgroundColorsField = (
		<>
			<div className={ 'field' }>
				{ ColorControl( {
					props,
					attribute: 'label_background_color_before',
					labelText: __( 'Before', 'wp-before-after-image-slider' ),
					resetText: __( 'Clear', 'wp-before-after-image-slider' ),
				} ) }
			</div>
			<div className={ 'field' }>
				{ isTriple &&
					ColorControl( {
						props,
						attribute: 'label_background_color_middle',
						labelText: __( 'Middle', 'wp-before-after-image-slider' ),
						resetText: __( 'Clear', 'wp-before-after-image-slider' ),
					} ) }
			</div>
			<div className={ 'field' }>
				{ ColorControl( {
					props,
					attribute: 'label_background_color_after',
					labelText: __( 'After', 'wp-before-after-image-slider' ),
					resetText: __( 'Clear', 'wp-before-after-image-slider' ),
				} ) }
			</div>
		</>
	);

	const BorderTypeField = (
		<SelectControl
			value={ props.attributes.label_border_style }
			onChange={ ( value ) => props.updateAttributes( {
				label_border_style: value,
			} ) }
			options={ [].map.call(
				[ 'none', 'solid', 'double', 'dotted', 'dashed', 'groove' ],
				( borderType ) => ( {
					// eslint-disable-next-line @wordpress/i18n-no-variables
					label: __( ucFirst( borderType ), 'wp-before-after-image-slider' ),
					value: borderType,
				} )
			) }
		/>
	);

	return (
		<PanelBody
			title={ __( 'Label', 'wp-before-after-image-slider' ) }
			initialOpen={ false }
			opened={ props.attributes.label_panel_open }
			onToggle={ () => props.updateAttributes( { label_panel_open: ! props.attributes.label_panel_open, } ) }
		>
			<PanelRow>
				<ProFeatureControl { ...props } value={ 'label_horizontal_alignment' }>
					{ props.attributes.slider_template === 'default' && (
						<div className={ 'field-control field-inline' }>
							<span className="field-control__label">
								{ __( 'ALIGNMENT:', 'wp-before-after-image-slider' ) }
							</span>
							<div className={ 'fields-wrapper inline' }>
								<LabelAlignmentControls />
							</div>
						</div>
					) }

					{ LabelAlign }

					<div className={ 'field-control field-inline' }>
						<span className="field-control__label">
							{ __( 'TEXT COLOR:', 'wp-before-after-image-slider' ) }
						</span>
						<div className={ 'fields-wrapper inline' }>
							{ LabelTextColorsField }
						</div>
					</div>
					<div className={ 'field-control field-inline' }>
						<span className="field-control__label">
							{ __( 'BACKGROUND COLOR:', 'wp-before-after-image-slider' ) }
						</span>
						<div className={ 'fields-wrapper inline' }>
							{ LabelBackgroundColorsField }
						</div>
					</div>
					<div className={ 'field-control field-inline' }>
						<span className="field-control__label">
							{ __( 'BACKGROUND GRADIENT:', 'wp-before-after-image-slider' ) }
						</span>
						<div className={ 'fields-wrapper inline' }>
							<div className={ 'field' }>
								<Button
									variant="tertiary"
									label={ __( 'Before', 'wp-before-after-image-slider' ) }
									showTooltip={ true }
									onClick={ () => updateToggleOptions( {
										...toggleOptions,
										background_gradient_before: ! toggleOptions.background_gradient_before,
									} ) }
									style={ { gap: '10px' } }
								>
									<ColorIndicator colorValue={ getAttr( 'label_background_gradient_before' ) } />
									{ __( 'Before', 'wp-before-after-image-slider' ) }
								</Button>
								{ toggleOptions.background_gradient_before && (
									<GradientPicker
										gradients={ [] }
										value={ getAttr( 'label_background_gradient_before' ) }
										onChange={ ( value ) => props.updateAttributes( { label_background_gradient_before: value, } ) }
									/>
								) }
							</div>
							{ isTriple && (
								<div className={ 'field' }>
									<Button
										variant="tertiary"
										label={ __( 'Middle', 'wp-before-after-image-slider' ) }
										showTooltip={ true }
										onClick={ () => updateToggleOptions( {
											...toggleOptions,
											background_gradient_middle: ! toggleOptions.background_gradient_middle,
										} ) }
										style={ { gap: '10px' } }
									>
										<ColorIndicator colorValue={ getAttr( 'label_background_gradient_middle' ) } />
										{ __( 'Middle', 'wp-before-after-image-slider' ) }
									</Button>
									{ toggleOptions.background_gradient_middle && (
										<GradientPicker
											gradients={ [] }
											value={ getAttr( 'label_background_gradient_middle' ) }
											onChange={ ( value ) => props.updateAttributes( { label_background_gradient_middle: value, } ) }
										/>
									) }
								</div>
							) }
							<div className={ 'field' }>
								<Button
									variant="tertiary"
									label={ __( 'After', 'wp-before-after-image-slider' ) }
									showTooltip={ true }
									onClick={ () => updateToggleOptions( {
										...toggleOptions,
										background_gradient_after: ! toggleOptions.background_gradient_after,
									} ) }
									style={ { gap: '10px' } }
								>
									<ColorIndicator colorValue={ getAttr( 'label_background_gradient_after' ) } />
									{ __( 'After', 'wp-before-after-image-slider' ) }
								</Button>
								{ toggleOptions.background_gradient_after && (
									<GradientPicker
										gradients={ [] }
										value={ getAttr( 'label_background_gradient_after' ) }
										onChange={ ( value ) => props.updateAttributes( { label_background_gradient_after: value, } ) }
									/>
								) }
							</div>
						</div>
					</div>

					<div className={ 'field-control field-inline' }>
						<span className="field-control__label">
							{ __( 'BORDER STYLE', 'wp-before-after-image-slider' ) }
						</span>
						<div className={ 'fields-wrapper inline' }>
							{ BorderTypeField }
						</div>
					</div>
					{ props.attributes.label_border_style !== 'none' && (
						<>
							<div className={ 'field-control field-inline' }>
								<span className="field-control__label">
									{ __( 'BORDER WIDTH:', 'wp-before-after-image-slider' ) }
								</span>
								<div className={ 'fields-wrapper inline' }>
									<div className={ 'group-field' } style={ { width: '100%', display: 'flex', gap: '10px', marginRight: '12px', } }>
										<UnitControl
											isPressEnterToChange
											onChange={ ( value ) => props.updateAttributes( { label_border_width: value, } ) }
											value={ getAttr( 'label_border_width' ) }
										/>
										<RangeControl
											onChange={ ( value ) => props.updateAttributes( {
												label_border_width: value,
											} ) }
											max={ 100 }
											min={ 0 }
											step={ 1 }
											allowReset={ true }
											withInputField={ false }
											value={ getNumberFromString( getAttr( 'label_border_width' ) ) }
										/>
									</div>
								</div>
							</div>
							<div className={ 'field-control field-inline' }>
								<span className="field-control__label">
									{ __( 'BORDER COLOR:', 'wp-before-after-image-slider' ) }
								</span>
								<div className={ 'fields-wrapper inline' }>
									<div className={ 'field' }>
										{ ColorControl( {
											props,
											attribute: 'label_border_color_normal',
											labelText: __( 'Default', 'wp-before-after-image-slider' ),
											resetText: __( 'Clear', 'wp-before-after-image-slider' ),
										} ) }
									</div>
								</div>
							</div>
						</>
					) }

					<div className={ 'field-control field-inline' }>
						<span className="field-control__label">
							{ __( 'PADDING:', 'wp-before-after-image-slider' ) }
						</span>
						<div className={ 'fields-wrapper inline' }>
							<div className={ 'group-field' } style={ { width: '100%', display: 'flex', gap: '10px', marginRight: '12px', } }>
								<BoxControl
									label={ '' }
									values={ getAttr( 'label_padding' ) }
									onChange={ ( value ) => props.updateAttributes( { label_padding: value, } ) }
								/>
							</div>
						</div>
					</div>
				</ProFeatureControl>
			</PanelRow>
		</PanelBody>
	);
};
