// External dependencies
import React from 'react';

// WordPress dependencies
// eslint-disable-next-line @wordpress/no-unsafe-wp-apis
import { __experimentalUnitControl as UnitControl, Button, ColorIndicator, GradientPicker, PanelBody, PanelRow, RangeControl, SelectControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';

// Internal dependencies
import ProFeatureControl from '../elements/pro-feature-control';
import { getNumberFromString, ucFirst } from '../../utils';
import ColorControl from '../control/color-control';

export default ( props ) => {
	const [ toggleOptions, updateToggleOptions ] = useState( {} );

	const getAttr = ( attribute ) => {
		const store = props.attributes;
		return !! store[ attribute ] ? store[ attribute ] : '';
	};

	const HandleBorderField = (
		<>
			<div className={ 'field-control field-inline' }>
				<span className={ 'field-control__label' }>
					{ __( 'BORDER STYLE', 'wp-before-after-image-slider' ) }
				</span>
				<div className={ 'fields-wrapper inline' }>
					<SelectControl
						value={ props.attributes.handle_border_style }
						onChange={ ( value ) => props.updateAttributes( { handle_border_style: value, } ) }
						options={ [].map.call(
							[ 'none', 'solid', 'double', 'dotted', 'dashed', 'groove', ],
							( borderType ) => ( {
								// eslint-disable-next-line @wordpress/i18n-no-variables
								label: __( ucFirst( borderType ), 'wp-before-after-image-slider' ),
								value: borderType,
							} )
						) }
					/>
				</div>
			</div>
			{ props.attributes.handle_border_style !== 'none' ? (
				<>
					<div className={ 'field-control field-inline' }>
						<span className={ 'field-control__label' }>
							{ __( 'BORDER WIDTH:', 'wp-before-after-image-slider' ) }
						</span>
						<div className={ 'fields-wrapper inline' }>
							<div className={ 'group-field' } style={ { width: '100%', display: 'flex', gap: '10px', marginRight: '12px', } }>
								<UnitControl
									isPressEnterToChange
									onChange={ ( value ) => props.updateAttributes( { handle_border_width: value, } ) }
									value={ getAttr( 'handle_border_width' ) }
								/>
								<RangeControl
									onChange={ ( value ) => props.updateAttributes( { handle_border_width: value, } ) }
									max={ 100 }
									min={ 0 }
									step={ 1 }
									allowReset={ true }
									withInputField={ false }
									value={ getNumberFromString( getAttr( 'handle_border_width' ) ) }
								/>
							</div>
						</div>
					</div>
					<div className={ 'field-control field-inline' }>
						<span className={ 'field-control__label' }>
							{ __( 'BORDER COLOR:', 'wp-before-after-image-slider' ) }
						</span>
						<div className={ 'fields-wrapper inline' }>
							<div className={ 'field' }>
								{ ColorControl( {
									props,
									attribute: 'handle_border_color_normal',
									labelText: __( 'Default', 'wp-before-after-image-slider' ),
									resetText: __( 'Clear', 'wp-before-after-image-slider' ),
								} ) }
							</div>
							<div className={ 'field' }>
								{ ColorControl( {
									props,
									attribute: 'handle_border_color_hover',
									labelText: __( 'Hover', 'wp-before-after-image-slider' ),
									resetText: __( 'Clear', 'wp-before-after-image-slider' ),
								} ) }
							</div>
						</div>
					</div>
				</>
			) : null }
		</>
	);

	return (
		<PanelBody
			title={ __( 'Handle', 'wp-before-after-image-slider' ) }
			initialOpen={ false }
			opened={ props.attributes.handle_panel_open }
			onToggle={ () => props.updateAttributes( { handle_panel_open: ! props.attributes.handle_panel_open, } ) }
		>
			<PanelRow>
				<ProFeatureControl { ...props } value={ 'handle_icon_color_normal' }>
					{ props.attributes.slider_template !== 'style07' && (
						<div className={ 'field-control field-inline' }>
							<span className="field-control__label">
								{ __( 'ARROW COLOR:', 'wp-before-after-image-slider' ) }
							</span>
							<div className={ 'fields-wrapper inline' }>
								<div className={ 'field' }>
									{ ColorControl( {
										props,
										attribute: 'handle_icon_color_normal',
										labelText: __( 'Default', 'wp-before-after-image-slider' ),
										resetText: __( 'Clear', 'wp-before-after-image-slider' ),
									} ) }
								</div>
								<div className={ 'field' }>
									{ ColorControl( {
										props,
										attribute: 'handle_icon_color_hover',
										labelText: __( 'Hover', 'wp-before-after-image-slider' ),
										resetText: __( 'Clear', 'wp-before-after-image-slider' ),
									} ) }
								</div>
							</div>
						</div>
					) }
					{ ! [ 'style03', 'style04', 'style06' ].includes( props.attributes.slider_template ) && (
						<>
							<div className={ 'field-control field-inline' }>
								<span className="field-control__label">
									{ __( 'BACKGROUND COLOR:', 'wp-before-after-image-slider' ) }
								</span>
								<div className={ 'fields-wrapper inline' }>
									<div className={ 'field' }>
										{ ColorControl( {
											props,
											attribute: 'handle_background_color_normal',
											labelText: __( 'Default', 'wp-before-after-image-slider' ),
											resetText: __( 'Clear', 'wp-before-after-image-slider' ),
										} ) }
									</div>
									<div className={ 'field' }>
										{ ColorControl( {
											props,
											attribute: 'handle_background_color_hover',
											labelText: __( 'Hover', 'wp-before-after-image-slider' ),
											resetText: __( 'Clear', 'wp-before-after-image-slider' ),
										} ) }
									</div>
								</div>
							</div>
							<div className={ 'field-control field-inline' }>
								<span className="field-control__label">
									{ __( 'BACKGROUND GRADIENT:', 'wp-before-after-image-slider' ) }
								</span>
								<div className={ 'fields-wrapper inline' }>
									<div className={ 'field' }>
										<Button
											variant="tertiary"
											label={ __( 'Default', 'wp-before-after-image-slider' ) }
											showTooltip={ true }
											onClick={ () => updateToggleOptions( {
												...toggleOptions,
												background_gradient_default: ! toggleOptions.background_gradient_default,
											} ) }
											style={ { gap: '10px' } }
										>
											<ColorIndicator colorValue={ getAttr( 'handle_background_gradient_normal' ) } />
											{ __( 'Default', 'wp-before-after-image-slider' ) }
										</Button>
										{ toggleOptions.background_gradient_default && (
											<GradientPicker
												gradients={ [] }
												value={ getAttr( 'handle_background_gradient_normal' ) }
												onChange={ ( value ) => props.updateAttributes( { handle_background_gradient_normal: value, } ) }
											/>
										) }
									</div>
									<div className={ 'field' }>
										<Button
											variant="tertiary"
											label={ __( 'Hover', 'wp-before-after-image-slider' ) }
											showTooltip={ true }
											onClick={ () => updateToggleOptions( {
												...toggleOptions,
												background_gradient_hover: ! toggleOptions.background_gradient_hover,
											} ) }
											style={ { gap: '10px' } }
										>
											<ColorIndicator colorValue={ getAttr( 'handle_background_gradient_hover' ) } />
											{ __( 'Hover', 'wp-before-after-image-slider' ) }
										</Button>
										{ toggleOptions.background_gradient_hover && (
											<GradientPicker
												gradients={ [] }
												value={ getAttr( 'handle_background_gradient_hover' ) }
												onChange={ ( value ) => props.updateAttributes( { handle_background_gradient_hover: value, } ) }
											/>
										) }
									</div>
								</div>
							</div>
						</>
					) }

					{ ! [ 'style02', 'style04', 'style07' ].includes( props.attributes.slider_template ) && HandleBorderField }
				</ProFeatureControl>
			</PanelRow>
		</PanelBody>
	);
};
