// WordPress dependencies.
import { useState } from '@wordpress/element';
import { Notice } from '@wordpress/components';
import { Icon, image, trash } from '@wordpress/icons';
import { __ } from '@wordpress/i18n';

export default function( props ) {
	const [ uploadOptions, setUploadOptions ] = useState( { } );

	function isImage( url ) {
		return /\.(jpg|jpeg|png|webp|avif|gif|svg)$/.test( url );
	}

	const uploadMedia = ( event ) => {
		event.preventDefault();

		if ( ! event.target.closest( '.remove-icon' ) ) {
			if ( uploadOptions.frame ) {
				uploadOptions.frame.open();
				return;
			}

			// Create a new media frame
			// ref: https://codex.wordpress.org/Javascript_Reference/wp.media
			// ref: https://wp-mix.com/wordpress-image-upload-jquery/
			// ref: https://gist.github.com/RadGH/966f8c756c5e142a5f489e86e751eacb
			const frame = window.wp.media.frames.file_frame = window.wp.media( {
				title: __( 'Select or Upload an image', 'wp-before-after-image-slider-pro', ),
				button: { text: 'Select Image' },
				library: { type: 'image' },
				multiple: false,
			} );

			// Fire select event on user activity.
			frame.on( 'select', function() {
				const attachment = frame.state().get( 'selection' ).first().toJSON();
				if ( props.onChange && !! attachment.url && isImage( attachment.url ) ) {
					props.onChange( attachment );
					setUploadOptions( ( prevState ) => ( { ...prevState, attachment, invalidImage: false, noticeClose: false } ) );
				} else {
					setUploadOptions( ( prevState ) => ( { ...prevState, attachment, invalidImage: true, noticeClose: false } ) );
				}
			} );

			// Finally, open the modal on click
			frame.open();

			// Update state with the frame data.
			setUploadOptions( ( prevState ) => ( { ...prevState, attachment: { }, frame, noticeClose: false } ) );
		}
	};

	const onRemoveImage = () => {
		if ( props.onChange ) {
			props.onChange( null );
		}
	};

	// Placeholder image styles.
	const ImageStyles = { margin: '0 auto', width: '100%', height: '60%', verticalAlign: 'middle', marginTop: '10px' };

	const viewImage = () => {
		// Render broken uploader placeholder to preview panel.
		if ( !! uploadOptions.invalidImage || ( !! props.value && ! isImage( props.value.url ) ) ) {
			return <Icon icon={ image } className={ 'placeholder-broken-image' } style={ ImageStyles } />;
		}

		// Render uploaded image to preview panel.
		if ( props.value && props.value.url && isImage( props.value.url ) ) {
			const imageSrcUrl = props.value.sizes && props.value.sizes.thumbnail ? props.value.sizes.thumbnail.url : props.value.url;
			return <img src={ imageSrcUrl } alt={ props.value.alt } />;
		}

		// Render uploader placeholder to preview panel.
		return <Icon icon={ image } className={ 'placeholder-upload-image' } style={ ImageStyles } />;
	};

	return (
		<div className={ 'coca-upload-media__component' }>
			<div className={ 'coca-upload-media' } onClick={ uploadMedia }>
				{ ! uploadOptions.invalidImage && props.value && props.value.url && isImage( props.value.url ) && (
					<a className={ 'remove-icon' } href={ '#' } onClick={ onRemoveImage }><Icon icon={ trash } /></a>
				) }
				{ viewImage() }
				<div className={ 'overlay' }>
					<div className='footer'>{ __( 'Choose a image', 'wp-before-after-image-slider-pro' ) }</div>
				</div>
			</div>
			{ ( !! uploadOptions.invalidImage || ( !! props.value && ! isImage( props.value.url ) ) ) && ! uploadOptions.noticeClose && (
				<Notice
					status="error"
					politeness="assertive"
					onRemove={ () => setUploadOptions( ( prevState ) => ( { ...prevState, noticeClose: true } ) ) }
					spokenMessage={ `Filetype ${ uploadOptions.attachment.mime } is not supported for Image Slider. Please select an Image file.` }>
					Filetype { uploadOptions.attachment.mime } is not supported for Image Slider. Please select an Image file.
				</Notice>
			) }
		</div>
	);
}

