// WordPress dependencies
import { PanelBody, PanelRow, SelectControl, TextControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

// Internal dependencies
import { ucFirst } from '../../utils';
import MediaUploader from '../elements/media-uploader';
import { stripAllTags } from '../../../../common/scripts/utils';

export default ( props ) => {
	const isTripleEnabled = !! props.proSettings.is_triple_active;
	const sliderType = props.attributes.slider_type;
	const isTriple = isTripleEnabled && sliderType === 'triple';
	let tabTitle = __( 'Before & After Images', 'wp-before-after-image-slider' );

	if ( isTripleEnabled ) {
		tabTitle = __( 'Comparable Images', 'wp-before-after-image-slider' );
	}

	const SliderTypeField = (
		<div className={ 'field-control field-inline' }>
			<span className="field-control__label">
				{ __( 'SLIDER TYPE', 'wp-before-after-image-slider' ) }
			</span>
			<div className={ 'fields-wrapper inline' }>
				<SelectControl
					value={ props.attributes.slider_type }
					onChange={ ( value ) => props.updateAttributes( { slider_type: value } ) }
					options={ [
						{ label: 'Two Image', value: 'default' },
						{ label: 'Three Image', value: 'triple' },
					] }
				/>
			</div>
		</div>
	);

	const ImageSizeField = (
		<div className={ 'field-control field-inline' }>
			<span className="field-control__label">
				{ __( 'IMAGE SIZE', 'wp-before-after-image-slider' ) }
			</span>
			<div className={ 'fields-wrapper inline' }>
				<SelectControl
					value={ props.attributes.image_size || 'full' }
					onChange={ ( value ) => props.updateAttributes( { image_size: value } ) }
					options={ [].map.call( window.COCA_MEDIA_SIZES, ( size ) => ( { label: ucFirst( size ), value: size, } ) ) }
				/>
			</div>
		</div>
	);
	const ImageUploadField = ( options ) => (
		<div className={ 'field-control field-inline' }>
			<span className="field-control__label">{ options.labelText }</span>
			<div className={ 'fields-wrapper inline' }>
				<MediaUploader
					value={ options.value }
					onChange={ ( attachment ) => props.updateAttributes( { [ options.attribute ]: attachment, } ) }
				/>
			</div>
		</div>
	);
	const ImageLabelField = ( options ) => (
		<div className={ 'field-control field-inline' }>
			<span className="field-control__label">{ options.labelText }</span>
			<div className={ 'fields-wrapper inline' }>
				<TextControl
					value={ stripAllTags( options.value ) }
					onChange={ ( value ) => props.updateAttributes( { [ options.attribute ]: stripAllTags( value ) } ) }
				/>
			</div>
		</div>
	);

	return (
		<PanelBody
			title={ tabTitle }
			initialOpen={ false }
			opened={ props.attributes.before_after_panel_open }
			onToggle={ () => props.updateAttributes( { before_after_panel_open: ! props.attributes.before_after_panel_open } ) }
		>
			<PanelRow>
				{ isTripleEnabled && SliderTypeField }
				{ ! isTriple && (
					<>
						{ ImageUploadField( {
							labelText: __( 'BEFORE IMAGE', 'wp-before-after-image-slider' ),
							attribute: 'before_image',
							value: props.attributes.before_image,
						} ) }
						{ ImageUploadField( {
							labelText: __( 'AFTER IMAGE', 'wp-before-after-image-slider' ),
							attribute: 'after_image',
							value: props.attributes.after_image,
						} ) }
					</>
				) }
				{ isTriple && (
					<>
						{ ImageUploadField( {
							labelText: __( 'BEFORE IMAGE', 'wp-before-after-image-slider' ),
							attribute: 'before_image',
							value: props.attributes.before_image,
						} ) }
						{ ImageUploadField( {
							labelText: __( 'MIDDLE IMAGE', 'wp-before-after-image-slider' ),
							attribute: 'middle_image',
							value: props.attributes.middle_image,
						} ) }
						{ ImageUploadField( {
							labelText: __( 'AFTER IMAGE', 'wp-before-after-image-slider' ),
							attribute: 'after_image',
							value: props.attributes.after_image,
						} ) }
					</>
				) }

				<div className={ 'field-control field-gap' }></div>

				{ ImageSizeField }
				{ ! isTriple && (
					<>
						{ ImageLabelField( {
							labelText: __( 'BEFORE IMAGE LABEL', 'wp-before-after-image-slider' ),
							attribute: 'before_image_label',
							value: props.attributes.before_image_label,
						} ) }
						{ ImageLabelField( {
							labelText: __( 'AFTER IMAGE LABEL', 'wp-before-after-image-slider' ),
							attribute: 'after_image_label',
							value: props.attributes.after_image_label,
						} ) }
					</>
				) }
				{ isTriple && (
					<>
						{ ImageLabelField( {
							labelText: __( 'BEFORE IMAGE LABEL', 'wp-before-after-image-slider' ),
							attribute: 'before_image_label',
							value: props.attributes.before_image_label,
						} ) }
						{ ImageLabelField( {
							labelText: __( 'MIDDLE IMAGE LABEL', 'wp-before-after-image-slider' ),
							attribute: 'middle_image_label',
							value: props.attributes.middle_image_label,
						} ) }
						{ ImageLabelField( {
							labelText: __( 'AFTER IMAGE LABEL', 'wp-before-after-image-slider' ),
							attribute: 'after_image_label',
							value: props.attributes.after_image_label,
						} ) }
					</>
				) }
			</PanelRow>
		</PanelBody>
	);
};
